@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500&family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

a {
	color: inherit;
	text-decoration: none;
}

img {
	width: inherit;
}

button {
	font-family: inherit;
	font-size: inherit;
	color: inherit;
	font-weight: inherit;
	padding: 0;
	border: none;
	background-color: unset;
}

input {
	border: none;
	color: inherit;
	font-size: inherit;
	font-weight: inherit;
	font-family: inherit;
}
:root {
    --fonte-familia-corpo: 'Montserrat', sans-serif;
    --fonte-familia-logo: 'Pacifico', cursive;
    --fonte-familia-titulo-pagina: 'Pacifico', cursive;

    --fonte-peso-input: 300;
    --fonte-peso-cartao: 500;
    --fonte-peso-categorias: 500;
    --fonte-peso-menu-cabecalho-item: 500;

    --fonte-tamanho-logo: 1.75rem;
    --fonte-tamanho-cartao-titulo: 1.75rem;
    --fonte-tamanho-cartao-post-titulo: 1.375rem;
    --fonte-tamanho-input-label: 1.25rem;
    --fonte-tamanho-botao: 1.375rem;
    --fonte-tamanho-formulario-fieldset: 1.375rem;
    --fonte-tamanho-tabela-cabecalho: 1.375rem;
    --fonte-tamanho-titulo-pagina: 1.75rem;
    --fonte-tamanho-menu-cabecalho-item: 1.2rem;
    --fonte-tamanho-naoencontrado-texto: 1.25rem;

    --cor-primaria: #0071ea;
    --cor-secundaria: #d6eaff;
    --cor-contraste-escuro: #4D4D4D;
    --cor-contraste-claro: #fff;
    --cor-divisao: #dadada;
    --cor-aviso: #df2525;
    --cor-sucesso: #46bb42;

    --cor-categoria-bem-estar: #ff4949;
    --cor-categoria-comportamento: #368dff;

    --sombra: 0 5px 10px #55a6ff38;
}

@media(min-width: 800px) {
    :root {
        --fonte-tamanho-logo: 2.5rem;
        --fonte-tamanho-menu-cabecalho-item: 1rem;
    }
}

body {
	background-color: var(--cor-secundaria);
	color: var(--cor-contraste-escuro);
	font-family: var(--fonte-familia-corpo);
}

.container { 
	padding-right: 1rem;
	padding-left: 1rem;
}

.flex {
	display: flex;
}

.flex--centro {
	align-items: center;
	justify-content: center;
}

.flex--coluna {
	flex-direction: column;
}

.titulo-pagina {
	display: flex;
	align-items: center;
	font-family: var(--fonte-familia-titulo-pagina);
	font-size: var(--fonte-tamanho-titulo-pagina);
	background-color: var(--cor-contraste-claro);
    border-radius: 10px;
	box-shadow: var(--sombra);
	height: 4.75rem;
	margin-top: 1.25rem;
	margin-bottom: 2rem;
	padding-left: 2rem;
}

@media(min-width: 800px) {
  .container {
		padding-right: 2.5rem;
		padding-left: 2.5rem;	
	}
}

@media(min-width: 1200px) {
	.container {
		padding-left: calc((100vw - 900px)/2);
		padding-right: calc((100vw - 900px)/2);
	}
}

.lista-categorias {
    width: 100%;
    box-sizing: border-box;
    justify-content: space-evenly;
    margin-bottom: 2rem;
}

.lista-categorias__categoria {
    font-weight: var(--fonte-peso-categorias);
    background-color: var(--cor-contraste-claro);
    border-radius: 3rem;
    padding: 1rem;
}

.lista-categorias__categoria--bem-estar {
    color: var(--cor-contraste-claro);
    background-color: var(--cor-categoria-bem-estar);
}

.lista-categorias__categoria--comportamento {
    color: var(--cor-contraste-claro);
    background-color: var(--cor-categoria-comportamento);
}


.cartao {
    background-color: var(--cor-contraste-claro);
    border-radius: 10px;
    box-shadow: var(--sombra);
    width: 100%;
    max-width: 45rem;
    padding: 1.75rem;
    box-sizing: border-box;
    margin-bottom: 1rem;
}

.cartao__titulo {
    font-size: var(--fonte-tamanho-cartao-titulo);
    margin-bottom: 1rem;
}

.cartao__texto {
    line-height: 1.2rem;
}

.cartao-post {
    background-color: var(--cor-contraste-claro);
    border-radius: 10px;
    box-shadow: var(--sombra);
    width: 100%;
    max-width: 25rem;
    padding: 1rem 2rem 2rem;
    box-sizing: border-box;
    margin-bottom: 2.25rem;
    border-top: 1rem solid;
}

.cartao-post__titulo {
    font-size: var(--fonte-tamanho-cartao-post-titulo);
    font-weight: var(--fonte-peso-cartao);
    margin-bottom: 1rem;
}

.cartao-post__meta {
    line-height: 1.3rem;
}

.cartao-post--bem-estar {
    border-color: var(--cor-categoria-bem-estar);
}

.cartao-post--comportamento {
    border-color: var(--cor-categoria-comportamento);
}

.posts {
    display: flex;
    flex-direction: column;
    align-items: center;
}

@media(min-width: 900px) {
    .posts {
        align-items: stretch;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-evenly;
    }
}
.doguito-imagem {
  width: 100%;
  max-width: 25rem;
  margin-top: 1.25rem;
  margin-bottom: 2rem;
}

.naoencontrado-texto {
  font-size: var(--fonte-tamanho-naoencontrado-texto);
  margin-bottom: 1rem;
}

.naoencontrado-link {
  color: var(--cor-primaria);
}
.post {
  margin-top: 1.25rem;
}

.cabecalho {
    background-color: var(--cor-contraste-claro);
    border-radius: 0 0 10px 10px;
    box-shadow: var(--sombra);
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: center;
    height: 4.75rem;
}

.menu-hamburguer {
    width: 1.5rem;
    height: .75rem;
    transition: .2s;
}

.menu-hamburguer--ativo .menu-hamburguer__icone {
    transform: rotate(90deg);
    transition: .2s;
}

.menu-hamburguer__icone {
    display: block;
    width: 100%;
    height: 100%;
    background-image: url(/static/media/menu.bcbef95e.svg);
    background-size: contain;
}

.cabecalho__logo {
    width: 3rem;
}

.cabecalho__titulo {
    font-family: var(--fonte-familia-logo);
    font-size: var(--fonte-tamanho-logo);
    margin-left: .5rem;
}

.menu-cabecalho-background {
    background-color: #00000020;
    position: fixed;
    top: 0;
    left: -100vw;
    width: 100vw;
    height: 100vh;
}

.menu-cabecalho {
    position: fixed;
    left: -100vw;
    top: 0;
    background-color: var(--cor-contraste-claro);
    width: 70vw;
    height: 100vh;
    box-sizing: border-box;
    font-size: var(--fonte-tamanho-menu-cabecalho-item);
    font-weight: var(--fonte-peso-menu-cabecalho-item);
    z-index: 10;
    transition: .25s;
}

.menu-cabecalho__fechar {
    position: absolute;
    right: -2.5rem;
    top: .5rem;
    color: var(--cor-contraste-claro);
    padding: 1rem;
    box-sizing: border-box;
    border-radius: 50%;
    background-color: var(--cor-primaria);
}

.menu-cabecalho__fechar::before {
    content: 'X';
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.menu-item {
    display: block;
    padding-top: 1.5rem;
    padding-left: 1rem;
    padding-bottom: 1.5rem;
    border-bottom: 1px solid var(--cor-divisao);
}

.menu-item--entrar {
    background-color: var(--cor-primaria);
    color: var(--cor-contraste-claro);
    border: none;
}

.menu-cabecalho--ativo {
    left: 0;
    top: 0;
    transition: .25s;
}

.menu-cabecalho--ativo + .menu-cabecalho-background {
    left: 0;
}

@media(min-width: 900px) {
    .cabecalho {
        grid-template-areas: "menu logo .";
        height: 6.75rem;
    }

    .cabecalho-container {
        grid-area: logo;
    }

    .menu-hamburguer {
        display: none;
    }

    .menu-cabecalho {
        justify-self: flex-start;
        position: static;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        grid-area: menu;
    }

    .menu-cabecalho__fechar {
        display: none;
    }
    
    .menu-cabecalho-background {
        display: none;
    }

    .menu-itens {
        display: flex;
        align-items: center;
    }

    .menu-item {
        border: none;
        padding: 0;
        margin-right: 1rem;
    }

    .menu-item--entrar {
        border-radius: 2rem;
        padding: 1rem 1.5rem;
    }
}

